import { gsap } from 'gsap'

function S(id) {
	const elements = document.querySelectorAll(id)
	return {
		prop: function(property, value) {
			for (let i = 0; i < elements.length; i++) {
				elements[i][property] = value
			}
			return this
		},
		node: elements[0],
		css: function(property, value) {
			for (let i = 0; i < elements.length; i++) {
				elements[i]['style'][property] = value
			}
			return this
		},
		text: function(value) {
			for (let i = 0; i < elements.length; i++) {
				elements[i]['innerHTML'] = value
			}
			return this
		},
		on: function(event, functionHandler) {
			for (let i = 0; i < elements.length; i++) {
				elements[i]['addEventListener'](event, functionHandler)
			}
			return this
		},
		id: function(value) {
			for (let i = 0; i < elements.length; i++) {
				elements[i]['id'] = value
			}
			return this
		},
		attr: function(property, value) {
			for (let i = 0; i < elements.length; i++) {
				elements[i].setAttribute(property, value)
			}
			return this
		},
		removeAttr: function(property) {
			for (let i = 0; i < elements.length; i++) {
				elements[i].removeAttribute(property)
			}
			return this
		},
		getStyle: function(property, toNumber = false) {
			if (property && !toNumber) {
				return window.getComputedStyle(elements[0]).getPropertyValue(property)
			} else if (toNumber && property) {
				return parseFloat(window.getComputedStyle(elements[0]).getPropertyValue(property))
			} else {
				return window.getComputedStyle(elements[0])
			}
		},
		getCssVar: function(variable, toNumber = false) {
			if (!toNumber) {
				return window.getComputedStyle(document.documentElement).getPropertyValue(variable)
			} else if (toNumber) {
				return parseFloat(window.getComputedStyle(document.documentElement).getPropertyValue(variable))
			}
		}
	}
}
function wait(t) {
	return new Promise(e => setTimeout(e, t))
}
function ProgressBar(injectTo, props) {
	props = props || {}
	props.input = props.input || 0
	props.shape = props.shape || 'circle' // circle , half, quart
	props.scale = props.scale || 0.9 // onlt for quart and half shape
	props.R = props.R || 90 // only for circle shape
	props.width = props.width || 15
	props.textColor = props.textColor || '#333333'
	props.textSize = props.textSize || 'default' // default , Number in px
	props.fontWeight = props.fontWeight || 'bold'
	props.fontFamily = props.fontFamily || 'sans-serif'
	props.backgroundColor = props.backgroundColor || '#cdd5d5'
	props.fillColor = props.fillColor || '#efefef'
	props.gradColor1 = props.gradColor1 || '#833ab4'
	props.gradColor2 = props.gradColor2 || '#fd1d1d'
	props.progressColor = props.progressColor || 'grad' // grad , (any color)
	props.shadow = props.shadow || false
	props.lineCap = props.lineCap || 'round' // round, square, butt, none
	props.startPoint = props.startPoint || 1 // 1, 2, 3, 4, only for circle shape

	if (injectTo === 'info') {
		console.log('function (\nelement selector to inject to,\nprops = ' + JSON.stringify(props, null, '\t') + ');')
	} else {
		const RandomIds = {
			PR: '',
			gardID: '',
			circleBGID: '',
			circleID: '',
			pathBGID: '',
			pathID: '',
			progress: '',
			progressBG: '',
			halfCircleID: '',
			halfCircleBgID: '',
			txt: ''
		}

		for (let i in RandomIds) RandomIds[i] = ~~(Math.random() * 99999999)

		const txtDy = props.shape === 'half' ? -(props.R / 7) : props.shape === 'quart' ? props.scale * 15 : props.R / 6
		const txtSize = props.shape === 'quart' || props.shape === 'half' ? props.scale * 45 : props.R / 2
		props.progressColor = props.progressColor === 'grad' ? `url(#${RandomIds.gardID})` : props.progressColor
		props.textSize = props.textSize === 'default' ? txtSize : props.textSize

		const circleShape = `
            <circle id='${RandomIds.circleBGID}' cx=${props.R + props.width / 2} cy=${props.R + props.width / 2} />
            <circle id='${RandomIds.circleID}' cx=${props.R + props.width / 2} cy=${props.R + props.width / 2} />`

		const quartShape = `
            <path id="${RandomIds.pathBGID}"
                d="M40,180c-24.29-18.23-40-47.28-40-80C0,44.77,44.77,0,100,0s100,44.77,100,100c0,32.72-15.71,61.77-40,80" />
            <path id="${RandomIds.pathID}"
                d="M40,180c-24.29-18.23-40-47.28-40-80C0,44.77,44.77,0,100,0s100,44.77,100,100c0,32.72-15.71,61.77-40,80" />`

		const halfShape = `
            <path fill = ${props.shape === 'half' ? props.fillColor : 'none'} transform="scale(${
			props.scale
		})" transform-origin="50% 50%" d="M0,99.5a100,100,0,0,1,200,0"/>
            <path id="${RandomIds.halfCircleBgID}" d="M0,99.5a100,100,0,0,1,200,0"/>
            <path id="${RandomIds.halfCircleID}" d="M0,99.5a100,100,0,0,1,200,0"/>`

		const inject = `  
            <svg class="${RandomIds.PR}" height="${props.R * 2 + props.width}" width="${props.R * 2 +
			props.width}" xmlns="http://www.w3.org/2000/svg">
                <linearGradient id="${RandomIds.gardID}" x1="0%" y1="0%" x2="0%" y2="100%">
                    <stop offset="0%" stop-color="${props.gradColor1}" />
                    <stop offset="50%" stop-color="${props.gradColor2}" />
                </linearGradient>
                ${props.shape === 'circle' ? circleShape : ''}
                <circle fill = ${props.shape === 'circle' || props.shape === 'quart' ? props.fillColor : 'none'} 
                    r = ${props.shape === 'quart' ? 100 : props.R - props.width / 2} 
                transform="scale(${props.shape === 'quart' ? props.scale : 1})" cx=100 cy=100 transform-origin="50% 50%" />
                ${props.shape === 'quart' ? quartShape : ''}
                ${props.shape === 'half' ? halfShape : ''}
                <text class='${RandomIds.txt}' x="50%" y="50%" dy=${txtDy} fill='${props.textColor}' 
                    text-anchor="middle" 
                    style="font-size:${props.textSize}; font-family:${props.fontFamily}; font-weight:${props.fontWeight};">${props.input}%</text>
            </svg>`

		document.querySelector(injectTo).innerHTML = inject

		const getIds = Object.assign({}, RandomIds)

		for (let i in getIds) {
			if (i.includes('ID')) {
				getIds[i] = document.getElementById(getIds[i].toString())
			} else getIds[i] = document.getElementsByClassName(getIds[i].toString())
		}

		const percentage = props.input > 100 ? 100 : props.input < 0 ? 0 : props.shape === 'half' ? props.input / 2 : props.input

		const circumference = props.shape === 'quart' ? 500 : props.shape === 'half' ? 629 : 2 * Math.PI * props.R

		const calcProgress = circumference - (percentage * circumference) / 100

		const rotate = props.startPoint === 1 ? -90 : props.startPoint === 2 ? 0 : props.startPoint === 3 ? 90 : props.startPoint === 4 ? 180 : 0

		const progressStyle = `
            r : ${props.R};
            stroke : ${props.progressColor};
            stroke-dasharray : ${circumference};
            stroke-dashoffset : ${circumference};
            stroke-width : ${props.width};
            transform-origin: 50% 50%;
            transform: rotate(${rotate}deg);
            stroke-linecap: ${props.lineCap};
            fill: none; `

		const progressBgStyle = `
            r : ${props.R};
            stroke : ${props.backgroundColor};
            stroke-width : ${props.width};
            transform-origin: 50% 50%;
            stroke-linecap: ${props.lineCap};
            fill: none;`

		if (props.shape === 'quart') {
			getIds.pathID.classList.add(RandomIds.progress)
			getIds.pathBGID.classList.add(RandomIds.progressBG)
		}
		if (props.shape === 'circle') {
			getIds.circleID.setAttribute('class', RandomIds.progress)
			getIds.circleBGID.setAttribute('class', RandomIds.progressBG)
		}
		if (props.shape === 'half') {
			getIds.halfCircleID.setAttribute('class', RandomIds.progress)
			getIds.halfCircleBgID.setAttribute('class', RandomIds.progressBG)
		}

		getIds.progress[0].style.cssText = progressStyle
		getIds.progressBG[0].style.cssText = progressBgStyle

		if (props.shape === 'quart' || props.shape === 'half') {
			getIds.progress[0].style.transform = `scale(${props.scale}) rotate(0deg)`
			getIds.progressBG[0].style.transform = `scale(${props.scale})`
		}
		if (props.shadow) getIds.PR[0].style.cssText = `filter: drop-shadow(2px 2px 7px rgba(0, 0, 0, 0.5))`

		gsap.to(getIds.progress, { strokeDashoffset: calcProgress, duration: 2, ease: 'back' })
	}
}
function isMobile() {
	if (
		navigator.userAgent.match(/Android/i) ||
		navigator.userAgent.match(/webOS/i) ||
		navigator.userAgent.match(/iPhone/i) ||
		navigator.userAgent.match(/iPod/i) ||
		navigator.userAgent.match(/iPad/i) ||
		navigator.userAgent.match(/BlackBerry/i) ||
		navigator.userAgent.match(/Windows Phone/i) ||
		navigator.userAgent.match(/Opera Mini/i) ||
		navigator.userAgent.match(/IEMobile/i) ||
		navigator.userAgent.match(/WPDesktop/i)
	) {
		return true
	} else {
		return false
	}
}
export { S, wait, ProgressBar, isMobile }
