import React, { Component } from "react";
import SearchPage from "./searchPage";
import { updateRenderState } from ".";
import { S, wait } from "./alabsi";

const foodCategories = {
	"الخبز و المعجنات": ["الخبز الفرنسي", "البسكوت", "بسكوت مالح", "المعجنات المحلاة والكرواسان", "الخبز الأسود", "الخبز الأبيض", "مخبوزات أخرى"],
	"البيض ومنتجات الحليب": ["الزبدة والدهون", "الجبن", "الجبن القريش", "الكريمة", "البيض", "السمن", "الحليب", "الألبان"],
	"لحم العجل": ["الصدر", "مفروم", "الأرجل", "الرق", "الضلوع", "الكتف", "أخرى"],
	"لحم البقر": ["الصدر", "الكتف", "مفروم", "الخاصرة", "الضلوع", "الرق", "الفخذ", "أخرى"],
	"لحم الخاروف": ["الصدر", "مفروم", "الأرجل", "الرق", "الضلوع", "الكتف", "أخرى"],
	"اللحوم": ["الدجاج", "المرتاديلا واللانشون", "السجق", "الديك الرومي والبط", "لحوم أخرى"],
	"الأسماك": ["الأسماك المعلبة", "الأسماك الطازجة", "الأسماك المصنعة"],
	"المشروبات": ["المشروبات الغازية", "القهوة والشاي", "مشروبات الطاقة", "المشروبات الساخنة", "العصائر", "مشروبات بالحليب", "الماء", "مشروبات أخرى"],
	"الفواكه والمكسرات": ["التفاح والموز والايجاص", "البرقوق والمشمش", "التوت والكرز", "فواكه معلبة", "فواكه مجففة", "الحمضيات", "أجنبية", "العنب والبطيخ", "المكسرات", "فواكه أخرى", "الخوخ"],
	"الوجبات جاهزة": [
		"الوجبات السريعة",
		"الهامبرغر",
		"المكسيكانو",
		"كعك المافن والبسكوت والكرواسان",
		"وجبات جاهزة أخرى",
		"البان كيك والوافل",
		"المعكرونة",
		"الفطائر واللفات",
		"البيتزا",
		"وجبات البطاطا",
		"أكل مطاعم",
		"سلطات",
		"سندويشات"
	],
	"الشوربات والصلصات": ["تبلات السلطات", "الصلصات", "الشوربات", "صلصة الطماطم", "مرقات العظم", "الخل"],
	"الوجبات الخفيفة والحلويات": ["الكاتو", "حلوى السكر", "الشوكولاتة", "البسكوت", "رقائق البطاطا والفشار", "الدونات وكعك المافن", "البوظة", "الحلويات", "الفطائر المحلاة", "حلويات أخرى"],
	"الخضروات": ["الحبوب والبقوليات", "الملفوف", "الخضروات معلبة", "الخضروات الخضراء", "البصل والثوم والزنجبيل", "الفليفلة", "المخللات والزيتون", "البطاطا", "الخضروات الجذرية", "البندورة"],
	"خزانة الطعام": ["حبوب الفطور (السيريل)", "النودل", "الطحين والسكر", "البقوليات", "المعكرونة", "الرز", "المربى والعسل", "نباتي", "أخرى"]
};

let pathnameTmp;

export default class CategoriesList extends Component {
	constructor() {
		super();
		this.state = {
			renderList: this.mapListHandle(Object.entries(foodCategories).map(e => e[0]))
		};
	}

	async componentDidMount() {
		pathnameTmp = window.location.hash.split("#")[1]; // breakfast, lunch, dinner, snacks

		// animation
		S("#categoriesListBody li").css("transition", "0");
		S("#categoriesListBody li").css("transform", "scaleY(0)");
		await wait(20);
		S("#categoriesListBody li").removeAttr("style");
	}

	mapListHandle = arr =>
		arr.map(e => (
			<li key={Math.random() * 100} onClick={this.onClickListHandle}>
				{e}
			</li>
		));

	onClickListHandle = async e => {
		if (foodCategories[e.target.innerHTML]) {
			const elTxt = e.target.innerHTML;
			S("#categoriesListPageHeaderTitle").text(elTxt);
			// animation
			S("#categoriesListBody li").css("transform", "scaleY(0)");
			await wait(200);

			this.setState({ renderList: this.mapListHandle(foodCategories[elTxt]) });
			// animation
			S("#categoriesListBody li").css("transition", "0");
			S("#categoriesListBody li").css("transform", "scaleY(0)");
			await wait(20);
			S("#categoriesListBody li").css("transform", "scaleY(1)");

			const getIndex =
				Object.entries(foodCategories)
					.map(e => e[0])
					.indexOf(elTxt) + 1;
			window.history.pushState(null, null, `/search${window.location.hash}#${getIndex}`);
		}
	};

	backButtonHandle = async _ => {
		if (window.location.hash.split("#")[3]) {
			// animation
			S("#categoriesListBody li").css("transform", "scaleY(0)");
			await wait(200);

			this.setState({ renderList: this.mapListHandle(Object.entries(foodCategories).map(e => e[0])) });
			S("#categoriesListPageHeaderTitle").text("الأصناف");
			// animation
			S("#categoriesListBody li").css("transition", "0");
			S("#categoriesListBody li").css("transform", "scaleY(0)");
			await wait(20);
			S("#categoriesListBody li").css("transform", "scaleY(1)");

			window.history.pushState(null, null, `/search#${pathnameTmp}#categoriesList`);
		} else {
			updateRenderState([<SearchPage key={Math.random() * 100} title={this.checkHash()} />]);
			window.history.pushState(null, null, `/search#${pathnameTmp}`);
		}
	};

	checkHash = _ => {
		let hash;
		switch (pathnameTmp) {
			case "breakfast":
				hash = "الفطور";
				break;
			case "lunch":
				hash = "الغداء";
				break;
			case "dinner":
				hash = "العشاء";
				break;
			case "snacks":
				hash = "وجبة خفيفة";
				break;
			default:
				break;
		}
		return hash;
	};

	render() {
		return (
			<div id='categoriesListPage'>
				<div id='categoriesListPageHeader'>
					<svg id='categoriesListPageBack' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' onClick={this.backButtonHandle}>
						<path d='M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z' />
					</svg>
					<p id='categoriesListPageHeaderTitle'>الأصناف</p>
				</div>
				<ul id='categoriesListBody'>{this.state.renderList}</ul>
			</div>
		);
	}
}
