import React, { Component, Fragment } from "react";
import ReactDOM from "react-dom";
import "./index.css";
import * as serviceWorker from "./serviceWorker";

import MainPage from "./mainPage";
import NavicationBar from "./NavicationBar";
import SearchPage from "./searchPage";
import CategoriesList from "./CategoriesList";

export var updateRenderState = function(v) {
	this.setState({ renderPage: v });
};

export default class Index extends Component {
	constructor() {
		super();
		this.state = {
			renderPage: [<MainPage key={Math.random() * 100} />, <NavicationBar key={Math.random() * 100} />]
		};
		updateRenderState = updateRenderState.bind(this);
	}
	componentDidMount() {
		window.addEventListener("popstate", this.checkURL);
	}

	checkURL = _ => {
		const switchArg = window.location.hash;
		if (switchArg === "#breakfast") {
			updateRenderState(<SearchPage key={Math.random() * 100} title={"الفطور"} />);
		} else if (switchArg === "#lunch") {
			updateRenderState(<SearchPage key={Math.random() * 100} title={"الغداء"} />);
		} else if (switchArg === "#dinner") {
			updateRenderState(<SearchPage key={Math.random() * 100} title={"العشاء"} />);
		} else if (switchArg === "#snacks") {
			updateRenderState(<SearchPage key={Math.random() * 100} title={"وجبة خفيفة"} />);
		} else if (switchArg.includes("categoriesList")) {
			updateRenderState(<CategoriesList key={Math.random() * 100} />);
		} else if (switchArg === "") {
			updateRenderState([<MainPage key={Math.random() * 100} />, <NavicationBar key={Math.random() * 100} />]);
		}
	};

	render() {
		return <Fragment>{this.state.renderPage}</Fragment>;
	}
}

ReactDOM.render(<Index />, document.getElementById("root"));
serviceWorker.unregister();
