import React, { Component } from "react";
import { updateRenderState } from ".";
import SearchPage from "./searchPage";

export default class Diary extends Component {
	mountSearchComponent = (ar, en) => {
		updateRenderState(<SearchPage key={Math.random() * 100} title={ar} />);
		window.history.pushState(undefined, undefined, "/search#" + en);
	};

	render() {
		return (
			<div id='pageBody'>
				<div className='mealName'>
					<h3 >الفطور {this.props.date}</h3>
				</div>
				<div className='mealFoods' onClick={_ => this.mountSearchComponent("الفطور", "breakfast")}>
					<div className='mealFoodsImgP'>
						<img src='./assets/bread.png' alt='bread' />
						<p>أضف طعامك</p>
					</div>
				</div>
				<div className='mealName'>
					<h3>الغداء</h3>
				</div>
				<div className='mealFoods' onClick={_ => this.mountSearchComponent("الغداء", "lunch")}>
					<div className='mealFoodsImgP'>
						<img src='./assets/soup.png' alt='soup' />
						<p>أضف طعامك</p>
					</div>
				</div>
				<div className='mealName'>
					<h3>العشاء</h3>
				</div>
				<div className='mealFoods' onClick={_ => this.mountSearchComponent("العشاء", "dinner")}>
					<div className='mealFoodsImgP'>
						<img src='./assets/fish.png' alt='fish' />
						<p>أضف طعامك</p>
					</div>
				</div>
				<div className='mealName'>
					<h3>وجبة خفيفة</h3>
				</div>
				<div className='mealFoods' onClick={_ => this.mountSearchComponent("وجبة خفيفة", "snacks")}>
					<div className='mealFoodsImgP'>
						<img src='./assets/banana.png' alt='banana' />
						<p>أضف طعامك</p>
					</div>
				</div>
			</div>
		);
	}
}
